import {
  Modal,
  Button,
  Form,
  Steps,
  DatePicker,
  Typography,
  Card,
  Row,
  Col,
} from "antd";

import { Input, InputNumber } from "antd";
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import { createProject } from "../../../action/project";
const { Step } = Steps;
const { Paragraph } = Typography;
const { RangePicker } = DatePicker;
const CreateModal = (props) => {
  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [duration, setDuration] = useState(1);
  const [description, setDescription] = useState("");
  const [unvalidForm, setUnValidForm] = useState(true);
  const [current, setCurrent] = useState(0);
  const [mileStoneName, setMileStoneName] = useState("mvp");
  const [mileStoneDuration, setMileStoneDuration] = useState(30);
  const [projectStartDate, setProjectStartDate] = useState("");
  const [projectEndDate, setProjectEndDate] = useState("");
  const [test, setTest] = useState()
  const handleOk = () => {
    setLoading(true);
    props.createProject({
      title: name, description, sprintDuration: duration, milestoneDuration: mileStoneDuration
      , startDate: projectStartDate, dueDate: projectEndDate
    });
    setLoading(false);
    props.setVisible(false);
  };

  const steps = [
    {
      title: "Project Name",
      content: (
        <>
          <Form.Item label="Project Title* (min length is 3 char)" hasFeedback htmlFor="p_title">
            <Input
              id="p_title"
              size="large"
              onChange={(e) => setName(e.target.value)}
              placeholder="project title "
              value={name}
            />
          </Form.Item>
          <Form.Item label="Project Description* (min length is 6 char)" hasFeedback htmlFor="p_desc">
            <Input.TextArea
              id="p_desc"
              size="large"
              value={description}
              onChange={(e) => setDescription(e.target.value)}
              placeholder="project description (min length is 8 char)"
              autosize={{ minRows: 4, maxRows: 6 }}
            />
          </Form.Item>
          <Form.Item
            label="Sprint Duration (day)"
            hasFeedback
            htmlFor="p_duration"
          >
            <InputNumber
              id="p_duration"
              style={{
                width: "60px",
              }}
              size="large"
              min={1}
              value={duration}
              defaultValue={duration}
              onChange={(val) => setDuration(val)}
            />
          </Form.Item>
          <Form.Item
            label="Project Start & Finish Date"
            hasFeedback
            htmlFor="p_se_date"
          >
            <RangePicker
              id="p_se_date"
              size="large"
              min={1}
              value={test}
              onChange={(v, val) => {
                setProjectStartDate(val[0]);
                setProjectEndDate(val[1]);
                setTest(v)
              }}
            />
          </Form.Item>
        </>
      ),
    },
    {
      title: "MilleStone",
      content: (
        <>
          <Form.Item label="MileStone Name * :" hasFeedback htmlFor="p_m_name">
            <Input
              id="p_m_name"
              size="large"
              value={mileStoneName}
              onChange={(e) => setMileStoneName(e.target.value)}
              placeholder="project milestone name  "
            />
          </Form.Item>
          <Form.Item
            label="MileStone Date (day after start project)"
            hasFeedback
            htmlFor="p_m_duration"
          >
            <InputNumber
              id="p_m_duration"
              style={{
                width: "60px",
              }}
              size="large"
              min={1}
              value={mileStoneDuration}
              defaultValue={mileStoneDuration}
              onChange={(val) => setMileStoneDuration(val)}
            />
          </Form.Item>
        </>
      ),
    },

    {
      title: "Info",
      content: (
        <>
          <Card>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Project Name :</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{name}</Paragraph>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Project Start Date :</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{projectStartDate}</Paragraph>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Project End Date :</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{projectEndDate}</Paragraph>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Project First MileStonr Name :</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{mileStoneName}</Paragraph>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Milestone Duration After Start :</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{mileStoneDuration}</Paragraph>
              </Col>
            </Row>
            <Row gutter={16}>
              <Col span={12}>
                <Paragraph>Project Description:</Paragraph>
              </Col>
              <Col span={12}>
                <Paragraph>{description}</Paragraph>
              </Col>
            </Row>
          </Card>
        </>
      ),
    },
  ];

  useEffect(() => {
    if (
      name.length >= 2 &&
      description.length >= 5 &&
      projectStartDate &&
      projectEndDate
    ) {
      setUnValidForm(false);
    } else {
      setUnValidForm(true);
    }
  }, [name, description]);

  return (
    <Modal
      maskStyle={{
        backgroundColor: "rgba(200,200,200,0.2)",
      }}
      visible={props.visible}
      title="Create Project Wizard"
      onCancel={() => props.setVisible(false)}
      footer={[
        <Button size="large" key="back" onClick={() => props.setVisible(false)}>
          Cancel
        </Button>,
        <Button
          disabled={unvalidForm}
          key="submit"
          size="large"
          type="primary"
          loading={loading}
          onClick={handleOk}
        >
          Create
        </Button>,
      ]}
    >
      <Steps
        current={current}
        type="navigation"
        onChange={(val) => setCurrent(val)}
        direction="horizontal"
        size="small"
      >
        {steps.map((item) => (
          <Step key={item.title} title={item.title} />
        ))}
      </Steps>
      <div className="steps-content">{steps[current].content}</div>
      <div className="steps-action">
        {current < steps.length - 1 && (
          <Button type="primary" onClick={() => setCurrent((c) => c + 1)}>
            Next Item
          </Button>
        )}

        {current > 0 && (
          <Button
            style={{ marginLeft: 8 }}
            onClick={() => setCurrent((c) => c - 1)}
          >
            Previous Item
          </Button>
        )}
      </div>
    </Modal>
  );
};

export default connect(null, { createProject })(CreateModal);
